function getLocale()
{
    let lang = 'en-US';

    lightdm.languages.forEach(l => {
        if (l.name.toLowerCase() === lightdm.language.toLowerCase()) {
            lang = l.code.split('.')[0].replace('_', '-');
        }
    });

    return lang;
}

function trans(key)
{
    return translations[getLocale().substring(0, 2)][key];
}

export {
    getLocale,
    trans
}